import "./App.css";
import { useState } from "react";
import { Chessboard } from "react-chessboard";
import { Chess } from "chess.js";
import { io } from "socket.io-client";

const socket = io("https://mint-gold-gargoyleosaurus.glitch.me/", {transports: ["websocket"]});

function Game() {
  const [chess, setChess] = useState(new Chess());
  const [position, setPosition] = useState(chess.fen());

  socket.on("game-full", ()=>{
    window.location.href = "about:blank";  })

  socket.on("opponent-name", opponent_name=>{
    document.getElementById("name_space").textContent = opponent_name
  })

  socket.on("self-opponent", opponent_for_me=>{
    document.getElementById("name_space").textContent = opponent_for_me
  })

  window.onload = () =>{
    const params = new URLSearchParams(window.location.search);
    const name = params.get("name")
    const id = params.get("id")
    socket.emit("join", [name, id])
    
    socket.emit("tell-color")
    document.getElementById("enemy_space").textContent = name
    socket.on("listen-color", colour => {
      document.getElementById("your_colour").textContent = `Your colour : ${colour}`
    })
  }

  window.beforeunload  = () =>{
    socket.emit("disconnect")
  }

  const checks = () => {
    if (chess.isGameOver()) {
      if (
        chess.isDraw() ||
        chess.isInsufficientMaterial() ||
        chess.isStalemate() ||
        chess.isThreefoldRepetition()
      ) {
        document.getElementById("game_status").textContent =
          "Game Status: Draw!";
      } else if (chess.isCheckmate()) {
        const winner = chess.turn() === "w" ? "Black" : "White";
        document.getElementById(
          "game_status"
        ).textContent = `Game Status: ${winner} won!`;
      }
    }
    return 0;
  };

  const move = (sourceSquare, targetSquare) =>{
    const old_fen = chess.fen()
    const move = chess.move({
      from: sourceSquare,
      to: targetSquare,
      promotion: 'q'
    });
    chess.load(old_fen)
    if (move === null) {
    } else {
      socket.emit("move", [sourceSquare, targetSquare])
    }
  }

  const handleMove = (sourceSquare, targetSquare) => {
    chess.move({
      from: sourceSquare,
      to: targetSquare,
      promotion: 'q'
    });
    setPosition(chess.fen());
    document.getElementById("game_status").textContent = "Game Status: Playing!";
    checks();
  };

  socket.on('user-disconnect', (opponent)=>{
    if (document.getElementById("name_space").textContent === opponent.name){
      document.getElementById("name_space").textContent=""
    }
  })

  socket.on("received-move", (move)=>{
    handleMove(move[0], move[1])
  })


  return (
    <>
      <div className="board_">
      <p className="name_space">Opponent: <span id="name_space"></span></p>
        <Chessboard
          id="BasicBoard"
          position={position}
          onPieceDrop={move}
          className="board"
          arePremovesAllowed={false}
        />
        <p className="name_space">You: <span id="enemy_space"></span></p>

      </div>
      <div className="your_colour" id="your_colour"></div>
      <div className="game_status" id="game_status">
        Game Status: Not Started yet!
      </div>
      <button
        className="btn"
        onClick={() => {
          setChess(new Chess());
          setPosition("rnbqkbnr/pppppppp/8/8/8/8/PPPPPPPP/RNBQKBNR");
          document.getElementById("game_status").textContent =
            "Game Status: Not Started yet!";
        }}
      >
        Reset
      </button>
    </>
  );
}

export default Game;